<template>
  <div>
    <tab-page ref="tabPage">
      <template slot="mobileMessageSearch">
        <data-table :data-table="dataTable" ref="dataTable">
          <div slot="conditions" style="margin-bottom:15px">
            <el-form :inline="true" :model="queryParams">
              <el-form-item label="手机号">
                <el-input v-model="queryParams.mobile"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSubmit" size="small" style="margin-top:0">查询
                </el-button>
              </el-form-item>
            </el-form>
          </div>
          <template slot="columns">
            <el-table-column prop="mobile" label="号码" width="150"></el-table-column>
            <el-table-column prop="sendTime" label="发送时间" width="200" :formatter="formatSendTime"></el-table-column>
            <el-table-column prop="content" label="短信内容" width="320"></el-table-column>
            <el-table-column prop="rsult" label="发送结果" width="120"></el-table-column>
            <el-table-column prop="receiveTme" label="接收时间" width="200" :formatter="formatReceiveTime"></el-table-column>
            <el-table-column prop="smsType" label="类型" width="120"></el-table-column>
            <el-table-column prop="errorMessage" label="错误内容" ></el-table-column>
            <el-table-column prop="errorCode" label="状态码" ></el-table-column>
          </template>
        </data-table>
      </template>
    </tab-page>
  </div>
</template>
<style scoped>
  .el-form-item__content .el-button {
    margin-top: 20px;
  }

  .el-table--border th {
    text-align: center;
  }

  .el-table th .cell {
    text-align: center;
  }

  .el-form--inline .el-form-item {
    margin-right: 2px;
  }

  .el-form-item {
    margin-bottom: 2px;
  }

  .el-dialog__footer {
    padding: 10px 0px;
    text-align: center;
  }

  .check-all-select {
    padding: 10px;
  }

  .check-all-select span.text-all {
    font-size: 14px;
    font-weight: bold;
  }

  .check-all-select label {
    float: right;
  }

  .mb-10 {
    margin-bottom: 10px;
  }
</style>
<script>
  import common from '../../../utils/common'
  import formatDate from '../../../utils/formatDate'
  import DataTable from "../../../components/DataTable"
  import TabPage from "../../../components/TabPage"
  export default{
    components: {
      DataTable,
      TabPage,
    },
    data(){
      return{
        queryParams:{
          mobile: '',//手机号
        },
        dataTable: {
          pageSize: 1000,
          pageSizes: [10, 20, 50, 100, 1000],
          url: "/web/v1/newsms/getrecordsbymobile",
          queryParams:{mobile:''},
          emptyText: '没有记录',
        },
      }

    },
    mounted(){
      var defaultPage = {label: "手机号短信查询", name: "mobileMessageSearch", slotName: 'mobileMessageSearch', closable: false};
      this.$refs.tabPage.$emit("open-tab", defaultPage);
    },
    methods: {
      onSubmit: function () {
        this.dataTable.queryParams = this.queryParams;
        var tag = this.warnFn(this.queryParams.mobile);
        if (tag) {
          return;
        }
        this.dataTable.queryParams.mobile = this.queryParams.mobile;
        this.dataTable.url = '/web/v1/newsms/getrecordsbymobile';
        this.$refs.dataTable.$emit('load-data');

      },
      warnFn(mobile){
        if (!mobile) {
          this.$notify({
            message: '手机号不能为空！',
            type: 'warning'
          });
          return true;
        }
        return false;
      },

      formatJson(filterVal, jsonData) {
        return jsonData.map(v => filterVal.map(j => {
          let returnValue;
          if (j == 'operator') {
            returnValue = v[j].name;
          } else {
            returnValue = v[j];
          }
          return returnValue;
        }))
      },

      formatSendTime(row){
          if(row['sendTime'] && row['sendTime'] != null){
            let sendtime = formatDate(new Date(row['sendTime']), 'yyyy-MM-dd hh:mm:ss');
            return sendtime;
          }else{
            return '--';
          }

      },
      formatReceiveTime(row){
        if(row['receivetime'] && row['receivetime'] != null){
          let receivetime = formatDate(new Date(row['reveiveTime']), 'yyyy-MM-dd hh:mm:ss');
          return receivetime;
        }else{
           return '--';
        }
      }

    }
  }
</script>
<style>
  .span-style {
    display: inline-block;
    margin-top: 8px;
    font-size: 14px;
  }
</style>
